import request from '@/utils/request'

// 查询列表
export function listTrade(query) {
  return request({
    url: '/trade/list',
    method: 'get',
    params: query
  })
}

// 查询
export function getTrade(id) {
  return request({
    url: '/trade/' + id,
    method: 'get'
  })
}

// 新增
export function addTrade(data) {
  return request({
    url: '/trade',
    method: 'post',
    data: data
  })
}

// 修改
export function updateTrade(data) {
  return request({
    url: '/trade',
    method: 'put',
    data: data
  })
}

// 删除
export function delTrade(id) {
  return request({
    url: '/trade/' + id,
    method: 'delete'
  })
}

