<template>
  <div class="app-container">
    <div class="title">Peter's homework for Trade Management</div>
    <div class="content">
      <el-form :model="queryParams" ref="queryForm" :inline="true" v-show="showSearch" label-width="68px">
        <el-form-item label="交易名称" prop="name">
          <el-input v-model="queryParams.name" placeholder="请输入交易名称" clearable size="small" />
        </el-form-item>
        <el-form-item>
          <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery">搜索</el-button>
          <el-button icon="el-icon-refresh" size="mini" @click="resetQuery">重置</el-button>
        </el-form-item>
      </el-form>

      <el-row :gutter="10" class="mb8">
        <el-col :span="1.5">
          <el-button type="primary" plain icon="el-icon-plus" size="mini" @click="handleAdd">新增</el-button>
        </el-col>
        <el-col :span="1.5">
          <el-button type="success" plain icon="el-icon-edit" size="mini" :disabled="single"
            @click="handleUpdate">修改</el-button>
        </el-col>
        <el-col :span="1.5">
          <el-button type="danger" plain icon="el-icon-delete" size="mini" :disabled="multiple"
            @click="handleDelete">删除</el-button>
        </el-col>
      </el-row>

      <el-table v-loading="loading" :data="list" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55" align="center" />
        <!-- <el-table-column label="id" align="center" prop="id" /> -->
        <el-table-column label="交易名称" align="center" prop="name" width="180" />
        <el-table-column label="交易类型" align="center" prop="type" width="180" >
          <template v-slot="scope">
            <span v-if="scope.row.type == '1'">存款</span>
            <span v-else-if="scope.row.type == '2'">消费</span>
            <span v-else>未知类型</span>
          </template>
        </el-table-column>
        <el-table-column label="交易金额" align="center" prop="price" width="180" />
        <el-table-column label="交易说明" align="center" prop="desc" />
        <el-table-column label="创建时间" align="center" prop="createTime" />
        <el-table-column label="修改时间" align="center" prop="updateTime" />
        <el-table-column label="操作" align="center" class-name="small-padding fixed-width">
          <template v-slot="scope">
            <el-button size="mini" type="text" icon="el-icon-edit" @click="handleUpdate(scope.row)">修改</el-button>
            <el-button size="mini" type="text" icon="el-icon-delete" @click="handleDelete(scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>

      <pagination v-show="total > 0" :total="total" :page="queryParams.pageNum" :limit="queryParams.pageSize"
        @update:page="queryParams.pageNum = $event" @update:limit="queryParams.pageSize = $event"
        @pagination="handlePagination" />

      <!-- 添加或修改对话框 -->
      <el-dialog :title="title" :visible.sync="open" width="500px" height="500px" append-to-body>
        <el-form ref="form" :model="form" :rules="rules" label-width="80px">
          <el-form-item label="交易名称" prop="name">
            <el-input v-model="form.name" placeholder="请输入交易名称" />
          </el-form-item>
          <el-form-item label="交易类型" prop="name">
            <el-select v-model="form.type" placeholder="请选择交易类型">
            <el-option
              v-for="dict in tradeOptions"
              :key="dict.value"
              :label="dict.label"
              :value="dict.value"
            ></el-option>
          </el-select>
          </el-form-item>
          <el-form-item label="交易金额" prop="name">
            <el-input-number v-model="form.price" placeholder="请输入交易金额" />
          </el-form-item>
          <el-form-item label="交易说明" prop="desc">
            <el-input v-model="form.desc" placeholder="请输入交易说明" />
          </el-form-item>
        </el-form>
        <div class="dialog-foIncidenter">
          <el-button type="primary" @click="submitForm">确 定</el-button>
          <el-button @click="cancel">取 消</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import { listTrade, getTrade, delTrade, addTrade, updateTrade } from "@/api/index";
import Pagination from "@/components/Pagination";
export default {
  name: "TradeManagement",
  components: {
    Pagination
  },
  data() {
    return {
      // 遮罩层
      loading: true,
      // 选中数组
      ids: [],
      // 非单个禁用
      single: true,
      // 非多个禁用
      multiple: true,
      // 显示搜索条件
      showSearch: true,
      // 总条数
      total: 0,
      // 表格数据
      list: [],
      // 弹出层标题
      title: "",
      // 是否显示弹出层
      open: false,
      //交易类型字典
      tradeOptions: [{
        "label": "存款",
        "value": "1"
      },
      {
        "label": "消费",
        "value": "2"
      }
      ],
      // 查询参数
      queryParams: {
        name: '',
        pageNum: 1,
        pageSize: 10
      },
      // 表单参数
      form: {},
      // 表单校验
      rules: {
      }
    };
  },
  created() {
    this.getList();
  },
  methods: {
    /** 查询列表 */
    getList(params = {}) {
      this.loading = true;
      const queryParams = {
        ...this.queryParams,
        ...params
      };
      listTrade(queryParams).then(response => {
        this.list = response.rows;
        this.total = response.total;
        this.loading = false;
      });
    },
    // 取消按钮
    cancel() {
      this.open = false;
      this.reset();
    },
    resetForm(refName) {
      if (this.$refs[refName]) {
        this.$refs[refName].resetFields();
      }
    },
    // 表单重置
    reset() {
      this.form = {
        id: null,
        name: null,
        desc: null
      };
      this.resetForm("form");
    },
    /** 搜索按钮操作 */
    handleQuery() {
      this.queryParams.pageNum = 1;
      this.getList();
    },
    /** 重置按钮操作 */
    resetQuery() {
      this.resetForm("queryForm");
      this.handleQuery();
    },
    // 多选框选中数据
    handleSelectionChange(selection) {
      this.ids = selection.map(item => item.id)
      this.single = selection.length !== 1
      this.multiple = !selection.length
    },
    /** 新增按钮操作 */
    handleAdd() {
      this.reset();
      this.open = true;
      this.title = "添加交易";
    },
    /** 修改按钮操作 */
    handleUpdate(row) {
      this.reset();
      const id = row.id || this.ids
      console.log(id,"11111111111")
      getTrade(id).then(response => {
        this.form = response.data;
        this.open = true;
        this.title = "修改交易";
      });
    },
    /** 提交按钮 */
    submitForm() {
      this.$refs["form"].validate(valid => {
        if (valid) {
          if (this.form.id != null) {
            updateTrade(this.form).then(() => {
              this.msgSuccess("修改成功");
              this.open = false;
              this.getList();
            });
          } else {
            addTrade(this.form).then(() => {
              this.msgSuccess("新增成功");
              this.open = false;
              this.getList();
            });
          }
        }
      });
    },
    /** 删除按钮操作 */
    handleDelete(row) {
      const ids = row.id || this.ids;
      this.$confirm('是否确认删除编号为"' + ids + '"的数据项?', "警告", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(function () {
        return delTrade(ids);
      }).then(() => {
        this.getList();
        this.msgSuccess("删除成功");
      })
    },
    msgSuccess(message) {
      this.$message({
        message: message,
        type: "success"
      });
    },
    selectDictLabel(options, value) {
      for (const option of options) {
        if (option.value === value) {
          return option.label;
        }
      }
      return "";
    },
    handlePagination({ page, limit }) {
      this.queryParams.pageNum = page;
      this.queryParams.pageSize = limit;
      this.getList();
    }
  }
};
</script>

<style scoped lang="less">
.app-container {
  margin: 50px;
}

.title {
  margin: 30px;
  font-size: 30px;
  text-align: center;
}

.content {
  border: 1px solid blue;
  padding: 30px;
}
</style>